.options-box {
  margin-top: 40px; 
  display:flex; 
  flex-direction: column;
  justify-content:center; 
  gap: 0.1rem;
  border: 2px solid #ffffff;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

.options-box > .title {
  font-weight: bold;
  background-color: #0CBFA7;
  border-radius: 10px 10px 0 0;
  padding: 10px;
}

