.body {
  padding: 200px;
  background-color: #3b404e;
  display: flex;
  justify-content: center;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}


/************* MAIN CONTAINER *************/

.container {
  /* GRID */
  display: grid;
  width: 1024px;
/*   height: 600px; */
  grid-template-columns: 100px minmax(120px, 1fr) repeat(4, 1fr);
  grid-template-rows: 30px repeat(2, 1em) minmax(auto, 300px) 30px 1fr;
  grid-gap: 0.5rem;
  grid-template-areas:
    "photo    account   post     post      post      post      "
    "photo    friends   post     post      post      post      "
    "photo    followers post     post      post      post      "
    "photo    .         post     post      post      post      "
    ".        .         likes    .         shares    shares    "
    "content  content   content  content   content   content   ";
}


.item {
  background-color: #33394C;
/*   background-image: linear-gradient(130deg, #6C52D9 0%, #1EAAFC 85%, #3EDFD7 100%); */
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
  color: #ffffff;
  border-radius: 4px;
/*   border: 2px solid #171717; */
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
}


.photo {
  grid-area: photo;

  border: none;
  box-shadow: none;
  background-color: transparent;
/*   background-image: url("https://i.ibb.co/nrKPtjN/Screenshot-2022-11-16-at-16-41-15.png"); */
  background-image: url("https://cdn2.iconfinder.com/data/icons/avatars-99/62/avatar-369-456321-64.png");
  background-size: contain;
  background-position: top center;
  background-repeat: no-repeat;
  border-radius: 0px;
}


.account {
  grid-area: account;

  border: none;
  box-shadow: none;
  background-color: transparent;
  justify-content: start;
}

.account p {
  font-weight: bold;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 20px;  
}


.friends {
  grid-area: friends;
}

.followers {
  grid-area: followers;
}

.friends,
.followers {
  border: none;
  box-shadow: none;
  background-color: transparent;
  justify-content: start;
  font-size: 14px;  
}

.friends span:before {
  content: "ðŸ‘¥ "
}

.followers span:before {
  content: "ðŸ—£ "
}


.post {
  grid-area: post;

  border: none;
  padding: 10px;
}

.post img {
  width: 100%;
  height: 100%;
  object-fit: contain;  
}


.likes, .shares, .views {
  border: none;
  box-shadow: none;
  background-color: transparent;  
  overflow: hidden;
  white-space: nowrap;
  align-self: start;
  justify-content: start;
  text-overflow: clip;
  font-size: 14px;
}

.likes {
  grid-area: likes;
}

.likes span:before {
  content: "ðŸ‘ "
}

.shares {
  grid-area: shares;
  justify-content: end;  
}

.views {
  grid-area: views;
}


.content {
  grid-area: content;
  box-shadow: none;
}



/************* MAIN CONTAINER > CONTENT CONTAINER *************/

.content-container {
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  grid-gap: 0.5rem;
  grid-template-areas:
    "messages responses";
}

.messages {
  grid-area: messages;
  padding: 10px;
}

.responses {
  grid-area: responses;
  padding: 10px;
}


/************* MAIN CONTAINER > CONTENT CONTAINER > MSG CONTAINER *************/

.msg-container {
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-columns: 50px 1fr;
  grid-template-rows: 50px;
/*   grid-auto-rows: minmax(auto); */
  grid-gap: 0.3rem;
/*   grid-template-areas:
    "msg-photo msg-1"
    ".         msg-2"
    ".         msg-3"; */
}


.msg-item {
  background-color: #3b404e;
/*   background-image: linear-gradient(130deg, #6C52D9 0%, #1EAAFC 85%, #3EDFD7 100%); */
/*   box-shadow: 
    0 3px 6px rgba(0,0,0,0.10), 
    0 3px 3px rgba(0,0,0,0.13)
  ; */
  color: #ffffff;
  border-radius: 4px;
  border: 1px solid #373737;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
}


.msg-photo {
  grid-column: 1;
  grid-row: 1 / span 2;

  border: none;
  box-shadow: none;
  background-color: transparent;
/*   background-image: url("https://i.ibb.co/nrKPtjN/Screenshot-2022-11-16-at-16-41-15.png"); */
  background-image: url("https://cdn0.iconfinder.com/data/icons/user-pictures/100/matureman2-128.png");
  background-size: contain;
  background-position: top center;
  background-repeat: no-repeat;
  border-radius: 0px;
}

.msg-name {
  grid-column: 2;
  grid-row: 1;

  border: none;
  background: none;
  box-shadow: none;
  justify-content: start;
  align-items: center;
  font-size: 16px;
  padding-left: 5px;
  font-weight: bold;
}

.msg {
  grid-column: 2;

  padding: 0px 10px;
  justify-content: start;
  align-items: center;
  font-size: 16px;
}

.msg p {
  display: block;
  width: 100%;
}


/************* MAIN CONTAINER > CONTENT CONTAINER > RESPONSE CONTAINER *************/

.res-container {
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-columns: 50px 1fr;
  grid-template-rows: 50px;
/*   grid-auto-rows: minmax(40px, auto); */
  grid-gap: 0.5rem;
/*   grid-template-areas:
    "msg-photo msg-1"
    ".         msg-2"
    ".         msg-3"; */
}


.res-item {
  background-color: #3b404e;
/*   background-image: linear-gradient(130deg, #6C52D9 0%, #1EAAFC 85%, #3EDFD7 100%); */
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
  color: #ffffff;
  border-radius: 4px;
  border: 1px solid #878787;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  padding: 10px;
}


.res-photo {
  grid-column: 1;
  grid-row: 1 / span 2;

  border: none;
  box-shadow: none;
  background-color: transparent;
/*   background-image: url("https://i.ibb.co/nrKPtjN/Screenshot-2022-11-16-at-16-41-15.png"); */
  background-image: url("https://cdn2.iconfinder.com/data/icons/avatars-99/62/avatar-369-456321-64.png");
  background-size: contain;
  background-position: top center;
  background-repeat: no-repeat;
  border-radius: 0px;

}


.res-prompt {
  grid-column: 2;
  grid-row: 1;

  border: none;
  background: none;
  box-shadow: none;
  justify-content: start;
  align-items: center;
  font-size: 16px;
  padding-left: 5px;
  font-weight: bold;
}


.response {
  grid-column: 2;
/*   grid-row: 2; */

  justify-content: start;
  text-align: left;
  background-color: #0C7DBF;  
}

.response:hover {
  background-image: linear-gradient(130deg, #0CBFA7 0%, #1EAAFC 85%, #3EDFD7 100%); 
  font-weight: bold;
}




.hidden {
  display: none;
}

.options-container {
  display: flex;
  justify-content: center;
  margin-top: 40px;
}
