label.option {
  cursor: pointer;
  padding: 5px;
}

label.option > span {
  vertical-align: middle;
  padding-left: 5px;
}

label.option:hover {
  color:#3EDFD7;
}


label.option > input[type="checkbox"] {
	font-size: 14px;
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	width: 2.5em;
	height: 1.5em;
	background: #ddd;
	border-radius: 3em;
	position: relative;
	cursor: pointer;
	outline: none;
	-webkit-transition: all .2s ease-in-out;
	transition: all .2s ease-in-out;
  vertical-align: middle;
}
  
label.option > input[type="checkbox"]:checked {
  background: lime;
}
  
label.option > input[type="checkbox"]:after{
  position: absolute;
  content: "";
  width: 1.5em;
  height: 1.5em;
  border-radius: 50%;
  background: #fff;
  -webkit-box-shadow: 0 0 .25em rgba(0,0,0,.3);
          box-shadow: 0 0 .25em rgba(0,0,0,.3);
  -webkit-transform: scale(.7);
          transform: scale(.7);
  left: 0;
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}
  
label.option > input[type="checkbox"]:checked:after{
	left: calc(100% - 1.5em);
}
